
.menu {
  position: fixed;
  top: 15px;
  right: 15px;
  z-index: 2;
}
.menu .logo {
  height: 70px;
  position: relative;
  min-width: 250px;
}
.menu .logo .score {
  font-size: 2em;
  display: inline-block;
  position: absolute;
  bottom: 15px;
  right: 60px;
}
.menu .logo img {
  width: 50px;
  float: right;
  position: relative;
}
.finish {
  text-align: right;
}
.challenge-container {
  margin: 100px 5px 0 5px;
  max-width: 600px;
}



